@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
:root {
  /* light mode */

  --text: hsl(200, 15%, 8%);
  --input: hsl(0, 0%, 52%);
  --background: hsl(0, 0%, 98%);
  --elements: hsl(0, 0%, 100%);

  --box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.08);
  --element-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.08);
  --btn-hover: 0px 0px 10px 5px var(--text);

  /* text */
  --items: 14px;
  --details: 16px;
}

[data-theme='dark'] {
  /* dark mode */
  --elements: hsl(209, 23%, 22%);
  --background: hsl(207, 26%, 17%);
  --text: hsl(0, 0%, 100%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nunito Sans', sans-serif;
}

body {
  background-color: var(--background);
  color: var(--text);
  font-size: var(--items);
}

main {
  padding: 50px 100px;
}

.countryWrapping {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  grid-auto-rows: 1fr;
  grid-gap: 5em;
  gap: 5em;
}

.countryWrapping a {
  color: var(--text);
  text-decoration: none;
}

.country {
  box-shadow: var(--box-shadow);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  background-color: var(--elements);
}

.flag {
  object-fit: cover;
  width: 100%;
  height: 185px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.info {
  padding: 20px;
}

.name {
  font-weight: 800;
  margin-bottom: 20px;
}

.info > div {
  margin-bottom: 5px;
}

.label {
  font-weight: 800;
}

@media (max-width: 480px) {
  main {
    padding: 40px 20px;
  }

  .countryWrapping {
    display: block;
  }
  .country {
    margin-bottom: 50px;
  }

  .flag {
    height: auto;
  }

  .btn {
    transition: none;
  }
}

header {
  background-color: var(--elements);
  box-shadow: var(--element-shadow);
  padding: 20px 100px;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-size: var(--details);
}

header > div {
  font-weight: 600;
}

.theme-toggle {
  cursor: pointer;
  padding: 10px 20px;
  border-radius: 5px;
}

@media (max-width: 480px) {
  header {
    padding: 20px 10px;
    font-size: 12px;
  }

  .theme-label {
    display: none;
  }
}

.filters {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 50px;
}

.search {
  padding: 10px 20px;
  border-radius: 5px;
  box-shadow: var(--element-shadow);
  background-color: var(--elements);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.search i {
  margin-right: 20px;
}

.input {
  width: 100%;
  border: none;
  background-color: var(--elements);
  padding-left: 5px;
}

.input::-webkit-input-placeholder {
  color: var(--text);
}

.input:-ms-input-placeholder {
  color: var(--text);
}

.search,
.input::placeholder,
.input {
  color: var(--text);
}

.custom-select {
  background-color: var(--elements);
  padding: 10px 20px;
  cursor: pointer;
  position: relative;
  width: 200px;
}

.custom-select,
.custom-options {
  border-radius: 5px;
  box-shadow: var(--element-shadow);
}

.custom-select__trigger {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
  position: relative;
  width: 100%;
}

.arrow {
  display: inline;
}

.custom-options {
  position: absolute;
  display: block;
  background-color: var(--elements);
  opacity: 1;
  visibility: hidden;
  top: 50px;
  left: 0;
  width: 100%;
}

.custom-options.open {
  opacity: 1;
  visibility: visible;
}

.custom-option {
  position: relative;
  display: block;
  cursor: pointer;
  padding: 10px 20px;
  width: 100%;
}

.custom-option:first-of-type {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.custom-option:last-of-type {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.custom-option:hover {
  background-color: var(--text);
  color: var(--elements);
}

.custom-option.selected {
  background-color: var(--text);
  color: var(--elements);
}

@media (max-width: 480px) {
  .filters {
    display: block;
  }

  .search {
    width: 100%;
    margin-bottom: 50px;
  }
}

/* Details */
.details-wrapper {
  font-size: var(--details);
}

.back-button {
  border-radius: 5px;
  border: none;
  background-color: var(--elements);
  box-shadow: var(--box-shadow);
  padding: 10px 20px;
  font-weight: 600;
  margin-bottom: 50px;
  color: var(--text);
  cursor: pointer;
}

.country-details {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.country-details > * {
  max-width: 50%;
}

.details h2 {
  margin-bottom: 30px;
}

.country-details .flag {
  border-radius: 5px;
  height: auto;
  width: 100%;
  object-fit: contain;
}

.details {
  padding-left: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.details-info {
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 1fr;
  grid-gap: 10px 1em;
  gap: 10px 1em;
}

.border-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  margin-top: 100px;
}

.border-wrapper .label {
  margin-right: 20px;
}

.border {
  border-radius: 5px;
  background-color: var(--elements);
  box-shadow: var(--element-shadow);
  padding: 5px 10px;
  font-size: 0.7em;
  font-weight: 600;
  margin-right: 10px;
  margin-bottom: 10px;
  cursor: pointer;
}

.btn {
  transition: 0.2s;
}

.btn:hover {
  box-shadow: 0px 0px 10px 5px var(--input);
}

@media (max-width: 770px) {
  .country-details {
    display: block;
  }

  .country-details > * {
    max-width: none;
  }

  .details {
    padding: 0;
    display: block;
  }

  .details h2 {
    margin: 30px auto;
  }

  .border-wrapper {
    margin-top: 50px;
  }
}

@media (max-width: 480px) {
  .details-info {
    grid-template-columns: 1fr;
    grid-auto-rows: 1fr;
    grid-gap: 20px 1em;
  }

  .border-wrapper .label {
    width: 100%;
    margin-right: 0;
    margin-bottom: 20px;
  }

  .border {
    float: left;
  }
}

